@font-face {
  font-family: "Work Sans";
  src: url("../../fonts/work-sans-regular.woff2") format("woff2"), url("../../fonts/work-sans-regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Work Sans";
  src: url("../../fonts/work-sans-medium.woff2") format("woff2"), url("../../fonts/work-sans-medium.woff") format("woff");
  font-weight: $font-weight-bold;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Anton";
  src: url("../../fonts/anton-regular.woff2") format("woff2"), url("../../fonts/anton-regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
